
body {
  background-image: url('../image/bg.jpg');
  background-size: cover;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  

  display: grid;
  grid-template-columns: repeat(2 , 1fr);
  grid-gap:100px 100px;
  max-width:150vw;
  align-items: center;
  justify-content: center;
  margin-top: 25vh;
  width: 50%;
  height: 20vw;
  margin-left: 25%;

}


.container > div {
  background-color: #b5b8b6;
  font-size: 1.2vw;
  text-align: center;
  border-radius: 2vw;
  height: 400px;
  box-shadow: 0 10px 10px 5px rgba(0, 0, 0, 0.473);
  position: relative;
  width: 100%;
  overflow: hidden;
  align-items: center;
  justify-content: center;

}

.content {
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background-size: cover;
  margin:auto;
  align-items: center;
  justify-content: center;
  

  
}
.content > h3{
  color: rgb(255, 183, 0);
  font-size: 1.5vw;
}
.content > p{
  opacity: 0;
  transition: 1s;
  color: rgb(255, 255, 255);
  font-size: 0.7vw;
  padding: 5px;
}
.content > p:hover{
  opacity: 1;
  color: rgb(53, 134, 255);
  text-shadow: 1px 1px #ffffff;
}
.boxc{
  height: 20vw;
 
}
